import { Component, OnInit, Output, EventEmitter, Input, ViewChild, TemplateRef } from '@angular/core';
import { CheckedState, CheckableSettings } from '@progress/kendo-angular-treeview';
import { of } from 'rxjs/observable/of';
import { find } from 'lodash';
import { ControlService } from '../../../service/control.service';
@Component({
  selector: 'app-grid-field-editor',
  templateUrl: './grid-field-editor.component.html',
  styleUrls: ['./grid-field-editor.component.css'],
  providers: [ControlService]
})
export class GridFieldEditorComponent implements OnInit {
  @Output() closeModal = new EventEmitter<any>();
  @Input() value;
  @Input() editorParams = {};
  @ViewChild('gridFieldFooter') modalFooter: TemplateRef<any>;
  modalConfig = {
    title: '列编辑器',
    width: 900,
    height: 500
  };

  public checkedKeys: any[] = [];
  treeData: any[] = [];
  treekeyNameMap = {};
  public get checkableSettings(): CheckableSettings {
    return {
      checkChildren: true,
      checkParents: true,
      enabled: true,
      mode: 'multiple'
    };
  }
  expandedKeys: any[] = ['0'];
  public children = (dataItem: any) => of(dataItem.Children);
  public hasChildren = (dataItem: any): boolean => !!dataItem.Children;
  public isExpanded = (dataItem: any, index: string) => {
    return true;
  }



  ngOnInit() {
    if (!this.editorParams) {
      return;
    }
    this.treeData = this.editorParams['treeData'];

    // 勾选的标识为lableID
    if (this.value && this.value.length > -1) {
      this.value.forEach(field => {
        this.checkedKeys.push(field.dataField);
      });
    }

  }

  public isChecked = (dataItem: any, index: string): CheckedState => {
    if (this.containsItem(dataItem)) { return 'checked'; }

    if (this.isIndeterminate(dataItem.Children)) { return 'indeterminate'; }

    return 'none';
  }

  private containsItem(item: any): boolean {
    return this.checkedKeys.indexOf(item['labelID']) > -1;
  }

  private isIndeterminate(items: any[] = []): boolean {
    let idx = 0;
    let item;

    while (item = items[idx]) {
      if (this.isIndeterminate(item.Children) || this.containsItem(item)) {
        return true;
      }

      idx += 1;
    }

    return false;
  }
  // 树节点点击事件，记录key-name，方便添加节点时获取name
  handleChecking($event) {
    console.log('记录check', $event);
    const dataItem = $event.item.dataItem;
    this.treekeyNameMap[dataItem.labelID] = dataItem.Name;
  }


  constructor(private controlService: ControlService) { }


  clickOK() {
    // 识别选中的labelID,组织列数组
    console.log(this.checkedKeys);
    if (this.checkedKeys.length === 0) {
      alert('请选择字段');
      return;
    }
    this.checkedKeys.forEach(labelID => {

      let field = find(this.value, function (o) { return o.dataField === labelID; });
      if (!field && labelID !== this.treeData[0].labelID) { // 字段不在表格列中 且不是全选时的表名
        field = this.controlService.getControlMetaData('GridField');
        field.id = 'gridField_' + labelID;
        field.dataField = labelID;
        field.caption = this.treekeyNameMap[labelID];
        this.value.push(field);
      }
    });
    // 判断是否有字段删除
    for (let i = 0; i < this.value.length; i++) {
      const field = this.value[i];
      if (this.checkedKeys.indexOf(field.dataField) < 0) {
        this.value.splice(i, 1);
        i--;
      }
    }
    this.closeModal.emit(this.value);
  }

  clickCancel() {
    this.closeModal.emit();
  }
}



