import { Component, OnInit, ViewChild, ViewContainerRef, Input, OnChanges } from '@angular/core';
import { DesignElementCollection, DesignElement } from '../../../../entity/entity';
import { FormService } from '../../../../service/form.service';
import { ControlTreeService } from '../../../../service/control-tree.service';
import { DgWrapperComponent } from '../../../../components/dg-wrapper/dg-wrapper.component';
import { assign } from 'lodash';
import { ViewModelService } from '../../../../service/viewmodel.service';

@Component({
  selector: 'form-designer',
  templateUrl: './form-designer.component.html',
  styleUrls: ['./form-designer.component.css']
})
export class FormDesignerComponent implements OnInit, OnChanges {
  @Input() domJson;
  private focusedDomJson;
  private focusedElementNode: DesignElement;
  formData: DesignElementCollection;
  vmTreeData = [];
  commandData = {};
  leftTabActive = 'controlTree';
  bottomTabActive = 'formDesignerTab';
  designerCode = '';
  codeEditorOptions = {
    theme: 'vs-dark',
    language: 'json',
    formatOnType: true
  };
  @ViewChild('propertyPanel', { read: ViewContainerRef }) propertyContainer: ViewContainerRef;
  @ViewChild(DgWrapperComponent) private formWrapper: DgWrapperComponent;
  constructor(private formService: FormService, private controlTreeService: ControlTreeService,
    private viewModelService: ViewModelService) { }

  ngOnInit() {
    this.formService.initPropertyContainer(this.propertyContainer);
  }
  ngOnChanges() {
    if (this.domJson) {
      this.refreshFormAndTree();
      this.designerCode = JSON.stringify(this.domJson);
    }
  }
  changeLeftBar(key) {
    this.leftTabActive = key;
  }
  changeBottomBar(key) {
    if (key === 'codeEditorTab') { // 切到代码编辑器
      this.getLatestCodeDom();
    }
    if (this.bottomTabActive === 'codeEditorTab' && key !== 'codeEditorTab') {  // 从代码编辑器切回可视化设计器
      this.saveFormCodeEditor();
    }
    this.bottomTabActive = key;
  }
  private refreshFormAndTree() {
    this.formData = this.formService.mappingDom2DgForm(this.domJson);
    this.vmTreeData = this.viewModelService.getViewModelTree(this.domJson);

    // 重新focus树节点
    if (this.focusedElementNode) {
      setTimeout(() => {
        this.controlTreeService.triggerTreeFocus.next(this.focusedElementNode.id);
      });

    }
  }


  refreshCodeEditor($event) {
    // console.log($event);
    this.focusedElementNode = $event;
    // 获取Map中的dom结构
    this.focusedDomJson = this.formService.domDgMap.get($event.id);
    this.designerCode = JSON.stringify(this.focusedDomJson);

  }
  private getLatestCodeDom() {
    if (this.focusedElementNode && this.focusedDomJson) {
      this.designerCode = JSON.stringify(this.focusedDomJson);
    } else {
      this.designerCode = JSON.stringify(this.domJson);
    }
  }

  private saveFormCodeEditor() {
    // TODO  校验数据
    if (!this.designerCode) {
      return;
    }
    // 修改domDgMap，重新刷新form和控件树
    const newDomJson = JSON.parse(this.designerCode);
    if (this.focusedElementNode) {
      const oldDomJson = this.formService.domDgMap.get(this.focusedElementNode.id);
      assign(oldDomJson, newDomJson);
    } else {
      assign(this.domJson, newDomJson);
    }
    this.formWrapper.clearForm();
    this.refreshFormAndTree();
  }
  /**
   * 保存，主要解决在代码编辑器视图中修改dom后直接点保存的场景
   */
  saveForm() {
    if (this.bottomTabActive === 'codeEditorTab') {
      this.saveFormCodeEditor();
    }
  }

}



