import { Subject } from 'rxjs/Subject';

export class DesignElementCollection {

  public elementAppended: Subject<DesignElement>;

  public elementRemoved: Subject<any>;

  constructor(public elements: DesignElement[]) {
    this.elementAppended = new Subject<DesignElement>();
    this.elementRemoved = new Subject<any>();
  }

  public append(element: DesignElement) {
    this.elements.push(element);
    this.elementAppended.next(element);
  }
  public remove(index) {
    this.elements.splice(index, 1);
    this.elementRemoved.next(index);
  }
}

export interface DesignElement {
  id: string;
  type: string;
  contents?: DesignElementCollection;
  [propName: string]: any;
  elementFocused?: Subject<any>;
}



