import { Component, OnInit, Input } from '@angular/core';
import { ViewModelService } from '../../../../service/viewmodel.service';

@Component({
  selector: 'view-model-designer',
  templateUrl: './view-model-designer.component.html',
  styleUrls: ['./view-model-designer.component.css']
})
export class ViewModelDesignerComponent implements OnInit {

  @Input() domJson;
  vmTableTree = [];
  constructor(private viewModelService: ViewModelService) { }

  ngOnInit() {
    this.vmTableTree = this.viewModelService.getVMTree(this.domJson);
  }
  focusVMNode($event) {
    console.log($event);
  }

}



