import { Component, Input, ViewContainerRef, ComponentFactoryResolver, ViewChild, OnChanges, ComponentRef } from '@angular/core';
import { DesignElementCollection, DesignElement } from '../../entity/entity';


import { controlMap } from '../control-map';
@Component({
  selector: 'dg-wrapper',
  templateUrl: './dg-wrapper.component.html',
  styleUrls: ['./dg-wrapper.component.css']
})
export class DgWrapperComponent implements OnChanges {
  @Input() template: DesignElementCollection;
  @ViewChild('dgwrapper', { read: ViewContainerRef }) dgWrapper: ViewContainerRef;

  constructor(private viewContainerRef: ViewContainerRef,
    private componentFactoryResolver: ComponentFactoryResolver) { }

  ngOnChanges() {
    if (this.template && this.template.elements) {
      this.template.elements.forEach(element => this.appendElement(element));
      this.template.elementAppended.subscribe(element => {
        this.appendElement(element);
      });
      this.template.elementRemoved.subscribe(index => {
        this.removeElement(index);
      });
    }
  }

  appendElement(element: DesignElement) {
    const elementType = element.type;
    if (controlMap[elementType]) {
      const componentType = controlMap[elementType].type;
      const componentProcessor = controlMap[elementType].processor;
      const componentFactory = this.componentFactoryResolver.resolveComponentFactory(componentType);
      const componentRef: ComponentRef<any> = this.dgWrapper.createComponent(componentFactory);
      if (componentProcessor) {
        componentProcessor(componentRef, element);
        if (element.elementFocused && componentRef.instance.onClick) {
          element.elementFocused.subscribe(($event) => {
            componentRef.instance.onClick();
          });
        }
      }
    }
  }

  removeElement(index) {
    this.dgWrapper.remove(index);
  }

  /**
   * 重新绘制表单时先清空原表单
   */
  clearForm() {
    this.dgWrapper.clear();
  }


}



