import { CommandEditorHelper } from './command-editor.helper';
import { Task, Command, CommandList, CommandHandler } from './../entity/command-object';
import { Component, OnChanges, SimpleChanges, ViewChildren, ViewChild, QueryList, AfterViewInit, Input } from '@angular/core';
import { PanelBarItemModel, PanelBarItemComponent } from '@progress/kendo-angular-layout';

@Component({
  selector: 'app-command-editor',
  templateUrl: './command-editor.component.html',
  styleUrls: ['./command-editor.component.css']
})
export class CommandEditorComponent implements OnChanges, AfterViewInit {
  @Input() commandData: any;

  private _commandForSelectedTask: Command;
  selectedTask: Task;
  selectedCommand: Command;
  services = services;

  // for resizing
  panelHeight = 600;
  @ViewChild('left') left: any;

  @ViewChildren(PanelBarItemComponent) panelBarItems: QueryList<PanelBarItemComponent>;

  commands: CommandList;
  items = [];

  /**
   * 从Command对象获取Handler
   * 暂时一个Command对应一个Handler，直接取数组中的第一个元素，后面可能会增加特殊处理
   */
  private getHandler(command: Command): CommandHandler {
    return command.handlers[0];
  }
  constructor(private editorHelper: CommandEditorHelper) {
    // this.commands = manager.getCommands();
  }

  ngOnChanges(changes: SimpleChanges) {
    /* if (changes['co']) */
    // this.items = this.commands.
  }

  ngAfterViewInit() {
    this.panelHeight = this.left.element.nativeElement.clientHeight;
    if (this.commandData) {
      this.commands = new CommandList(this.commandData);
      this.items = this.commands.commandList.map(item => ({title: item.name, content: item}));
    }
  }

  click(data) {
    console.log(data);
    this.items.push({ title: '3', content: '3' });
  }

  centerStateChange(data: Array<PanelBarItemModel>) {
    const focusedEvent = data.filter(item => item.focused === true)[0];
    this.selectedCommand = focusedEvent.content;
    this.selectedTask = void 0;
    this._commandForSelectedTask = void 0;
  }

  serviceMethodDblClicked(data) {
    const task = new Task();
    console.log(data);
    task.name = data.node.data.name;
    this.editorHelper.addTask(task, this.getHandler(this.selectedCommand));
  }

  getTasks(dataItem: {content: Command}): Array<Task> {
    const handler = this.getHandler(dataItem.content);
    // console.log(handler);
    if (!handler) {
      throw Error('没有找到对应的CommandHandler');
    }
    return handler.tasks;
  }

  handleTaskSelection({ dataItem }: any, barItemData: any) {
    console.log(barItemData);
    this.selectedTask = dataItem;
    this.selectedCommand = void 0;
    this._commandForSelectedTask = barItemData.content;
    // console.log(this.panelBarItems);
  }

  handleTaskUnselect(data: any) {
    // console.log('blur');
    // this._lastSelectedTask = this._selectedTask;
    if (this.selectedTask) {
      // this.selectedTask = void 0;
    }
  }

  addCommand() {
    //
  }

  removeCommand() {
    //
  }

  addTask() {
    //
  }

  removeTask() {
    //
  }

  /**
   * 保存command的修改
   * 把Command对象保存成JSON数据
   */
  save() {
    this.commands.toJson();
  }

  private _createHandlerName(commandName: string): string {
    const firstLetter = commandName.substr(0, 1);
    const remainLetter = commandName.substr(1, commandName.length - 1);
    return firstLetter.toUpperCase() + remainLetter + 'Handler';
  }
}

export const services = [{
  id: 'SaleOrderListService',
  name: 'SaleOrderListService',
  children: [
    { id: 'load', name: 'load' },
    { id: 'remove', name: 'remove' },
    { name: 'route', id: 'route' },
    { name: 'search', id: 'search' }
  ]
}];



