import { Component, OnInit, Output, EventEmitter, Input, ViewChild, TemplateRef } from '@angular/core';

@Component({
  selector: 'app-icon-select-editor',
  templateUrl: './icon-select-editor.component.html',
  styleUrls: ['./icon-select-editor.component.css']
})
export class IconSelectEditorComponent {
  @Output() closeModal = new EventEmitter<any>();
  @Input() value;
  @ViewChild('iconSelectFooter') modalFooter: TemplateRef<any>;
  modalConfig = {
    title: '选择图标',
    width: 800,
    height: 400
  };
  constructor() { }

  clickOK() {
    this.closeModal.emit(this.value);
  }

  clickCancel() {
    this.closeModal.emit();
  }

}



