import { Component, Input, OnChanges } from '@angular/core';
import { Command } from '../entity/command-object';

@Component({
  selector: 'app-command-property',
  templateUrl: './command-property.component.html',
  styleUrls: ['./command-property.component.css']
})
export class CommandPropertyComponent {
  @Input() command: Command;
  paramsData: {rows: any[]};

  constructor() {
  }

  paramColumns: any[] = [
    {
      field: 'name',
      title: '参数名称',
      type: 'text',
      readonly: true
    }, {
      field: 'value',
      title: '参数值',
      type: 'text'
    }, {
      field: 'description',
      title: '说明',
      type: 'text',
      readonly: true
    }
  ];
}



