export class Service {
  name: string;
  alias: string;
  path: string;
  methods = new Map<string, Task>();
  constructor(serviceJson?: any) {
    if (!serviceJson) {
      this.name = '';
      this.alias = '';
      this.path = '';
      return;
    }
    this.name = serviceJson.name;
    this.alias = serviceJson.alias;
    this.path = serviceJson.path;
  }

  toJson() {
    return {
      'name': this.name,
      'alias': this.alias,
      'path': this.path
    };
  }

  // getMethod(name: string):  {

  // }
}

export class Task {
  name: string;
  method: string;
  service: Service;
  servicePath: string;
  params: any[];

  constructor(jsonObj?: any, services?: Map<string, Service>) {
    if (!jsonObj || !services) {
      this.name = '';
      this.method = '';
      this.service = new Service();
      this.servicePath = '';
      this.params = [];
      return;
    }

    this.name = jsonObj.name;
    this.method = jsonObj.methodName;
    this.params = new Array();
    for (const param of jsonObj.params) {
      this.params.push(Object.assign({}, param));
    }

    this.service = services.get(jsonObj.service);
    // if (this.service.getMethod()) // todo: 从构件获取服务名方法名，建立method对象。
  }
  // static parse(jsonObj: any): Task {
  //   const task = new Task();
  //   task.name = jsonObj.name;
  //   task.method = jsonObj.methodName;
  //   task.service = jsonObj.serviceName;
  //   task.params = new Array<any>();
  //   for (const param of jsonObj.params) {
  //     task.params.push(param);
  //   }
  //   return task;
  // }

  toJson(): any {
    const taskJson = {
      'name': this.name,
      'serviceName': this.service,
      'methodName': this.method,
      'params': []
    };
    for (const param of this.params) {
      taskJson.params.push(param);
    }
    return taskJson;
  }

  // clone(): Task {
  //   const result = new Task();
  //   result.name = this.name;
  //   result.method = this.method;
  //   result.params = new Array<any>();
  //   for (const param of this.params) {
  //     result.params.push(param);
  //   }
  //   return result;
  // }
}

export class CommandHandler {
  name: string;
  commandName: string;
  paramDesc: Array<{name: string, value: string, description: string}>;
  tasks: Array<Task>;

  constructor(handlerJsonObj: any, private _services: Map<string, Service>) {
    this.name = handlerJsonObj.name;
    this.commandName = handlerJsonObj.commandName;

    this.tasks = new Array<Task>();
    for (const taskJsonObj of handlerJsonObj.tasks) {
      const task = new Task(taskJsonObj, _services);
      this.tasks.push(task);
    }

    this.paramDesc = new Array<any>();
    if (!handlerJsonObj.paramsDesc) {
      return;
    }
    for (const paramDesc of handlerJsonObj.paramsDesc) {
      this.paramDesc.push(Object.assign({}, paramDesc));
    }
  }

  toJson(): any {
    // name / command
    const commandJson = {
      'name': this.name,
      'commandName': this.commandName,
      'services': [],
      'tasks': []
    };

    // task & service summary
    for (const task of this.tasks) {
      commandJson.tasks.push(task.toJson());
      if (!commandJson.services.find(item => item.name === task.service && item.path === task.servicePath)) {
        commandJson.services.push({name: task.service, path: task.servicePath});
      }
    }
    return commandJson;
  }

  clone() {
    const jsonObj = this.toJson();
    const handler = new CommandHandler(jsonObj, this._services);
    return handler;
  }
}

export class Command {
  name: string;
  handlers: Array<CommandHandler>;
  params: Array<any>;

  constructor(jsonObj: any, handlers: Map<string, CommandHandler>) {
    this.name = jsonObj.name;
    // command.commandHandler = jsonObj.commandHandler;
   this.params = new Array();
    if (jsonObj.params) {
      for (const param of jsonObj.params) {
        this.params.push(Object.assign({}, param));
      }
    }
    this.handlers = new Array();
    for (const handlerJsonObj of jsonObj.commandHandlers) {
      this.handlers.push(handlers.get(handlerJsonObj.name));
    }
  }

  toJson() {
    const commandJson = {
      'name': this.name,
      'commandHandlers': [this.handlers[0].name],
      'params': []
    };
    for (const param of this.params) {
      commandJson.params.push(Object.assign({}, param));
    }
  }
}

