export class CommandList {
  private _services = new Map<string, Service>();
  private _commandList = new Array<Command>();
  private _handlerList = new Map<string, CommandHandler>();

  constructor(jsonObj: any) {
    this._parse(jsonObj);
  }

  toJson() {
    const serviceListJson = [];
    this._services.forEach(item => serviceListJson.push(item.toJson()));

    const handlersJson = [];
    this._handlerList.forEach(item => handlersJson.push(item.toJson()));

    const commandsJson = [];
    this._commandList.forEach(item => commandsJson.push(item.toJson()));

    return {
      'serviceList': serviceListJson,
      'commandHandlers': handlersJson,
      'commands': commandsJson
    };
  }

  private _parse(jsonObj: any) {
    if (!jsonObj.commandHandlers || !jsonObj.commands) {
      console.error('命令对象信息不完整，无法解析。');
      return null;
    }

    for (const serviceJson of jsonObj.serviceList) {
      this._services.set(serviceJson.alias, new Service(serviceJson));
    }

    for (const handlerJson of jsonObj.commandHandlers) {
      this._handlerList.set(handlerJson.commandName, new CommandHandler(handlerJson, this._services));
    }

    for (const commandJson of jsonObj.commands) {
      const command = new Command(commandJson, this._handlerList);
      this.commandList.push(command);
    }
  }

  addCommand(command: Command) {
    this._commandList.push(command);
    for (const handler of command.handlers) {
      if (!this._handlerList.get(handler.name)) {
        this._handlerList.set(handler.name, handler);
      }
    }
  }

  get commandList(): Array<Command> {
    return this._commandList;
  }

  createCommandFromHandler(name: string, handler: CommandHandler): Command {
    const newHandler = handler.clone();
    newHandler.name = this._generateHandlerName(handler.name);
    this._handlerList.set(newHandler.name, newHandler);
    const commandJson = {
      'name': name,
      'commandHandlers': [newHandler.name],
      'params': []
    };
    const command = new Command(commandJson, this._handlerList);
    return command;
  }

  private _generateHandlerName(name: string) {
    name = this._getHandlerOriginName(name);
    let i = 1;
    let newName = '';
    while (true) {
      newName = name + i;
      if (!this._handlerList.get(newName)) {
        break;
      }
      ++i;
    }
    return newName;
  }

  private _getHandlerOriginName(name) {
    if (this._handlerList.get(name)) {
      // 已使用，先找出原始名
      if (name.length <= 1) {
        throw Error('生成Handler名称出错，名称长度不大于1且在Handler列表中已存在。');
      }
      name = name.slice(0, name.length - 2);
      // 如果序号增长到两位数字，需要递归调用一次，直到出错或者是没有被使用的原始handler名称。
      return this._getHandlerOriginName(name);
    }
    return name;
  }
}



