import { Component, OnInit } from '@angular/core';
import { HostListener, ElementRef, Renderer } from '@angular/core';
import { ControlTreeService } from '../../service/control-tree.service';
import { FormService } from '../../service/form.service';

@Component({
  selector: 'app-dg-basic',
  templateUrl: './dg-basic.component.html',
  styleUrls: ['./dg-basic.component.css']
})
export class DgBasicComponent implements OnInit {

  // 边框样式
  @HostListener('click', ['$event'])
  onClick($event) {
    if ($event) {
      $event.stopPropagation();
    }
    const elements = document.getElementsByClassName('dgComponentSelected');
    if (elements && elements.length > 0) {
      elements[0].classList.remove('dgComponentSelected');
    }
    this.renderer.setElementClass(this.el.nativeElement, 'dgComponentSelected', true);

    this.showProperty();
    if ($event) {
      this.triggerTreeFocus();
    }
  }
  constructor(public el: ElementRef, public renderer: Renderer, public formService: FormService,
    public controlTreeService: ControlTreeService) { }

  ngOnInit() {
  }

  /**
     * 触发属性面板展示
     */
  showProperty() {
  }
  /**
   * 触发控件树focus
   */
  triggerTreeFocus() {
  }
}



