import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { filter } from 'lodash';

@Injectable()
export class ControlService {
    constructor(private http: HttpClient) {
    }

    /**
     * 获取控件元数据(dom结构)
     */
    getControlMetaData(type) {
        switch (type) {
            case 'TextBox': {
                return {
                    'id': 'TextBox1',
                    'type': 'TextBox',
                    'appearance': null,
                    'border': null,
                    'font': null,
                    'margin': null,
                    'padding': null,
                    'size': null,
                    'binding': null,
                    'title': 'TextBox1',
                    'readonly': false,
                    'require': true,
                    'disable': false,
                    'placeHolder': '',
                    'format': null,
                    'validation': null,
                    'defaultValue': '',
                    'value': null,
                    'maxLength': null
                };
            }
            case 'Button': {
                return {
                    'id': 'button1',
                    'type': 'Button',
                    'appearance': null,
                    'border': null,
                    'font': null,
                    'margin': null,
                    'padding': null,
                    'size': null,
                    'disable': false,
                    'icon': null,
                    'iconPosition': null,
                    'seperate': true,
                    'text': 'Button1',
                    'textAligment': 'center',
                    'click': null,
                    'template': null
                };
            }
            case 'GridField': {
                return {
                    'id': 'col1',
                    'type': 'GridField',
                    'appearance': null,
                    'border': null,
                    'font': null,
                    'margin': null,
                    'padding': null,
                    'size': null,
                    'binding': null,
                    'dataField': '',
                    'displayTemplate': null,
                    'caption': '',
                    'captionTemplate': null,
                    'editor': null,
                    'readonly': false,
                    'require': false,
                    'draggable': false,
                    'frozen': false,
                    'sortable': false,
                    'sortOrder': null,
                    'resizeable': true
                };
            }
        }
    }
    /**
     * 获取控件列表
     * @param type
     */
    getControlList(controlName) {
        const subject = new Subject<any>();
        this.http.get<any[]>('assets/mock-data/controlList.json').subscribe(data => {
            if (data && data.length > 0 && controlName) {
                data.forEach(element => {
                    element.items = filter(element.items, function (o) { return o.name.includes(controlName); });
                });
            }
            subject.next(data);
        });
        return subject;
    }

}




