import { Component, Input, OnChanges } from '@angular/core';
import { Task } from '../entity/command-object';

@Component({
  selector: 'app-task-property',
  templateUrl: './task-property.component.html',
  styleUrls: ['./task-property.component.css']
})
export class TaskPropertyComponent implements OnChanges {
  @Input() task: Task;
  paramsData: {rows: any[]};

  constructor() {
  }

  paramColumns: any[] = [
    {
      field: 'name',
      title: '参数名称',
      type: 'text',
      readonly: false
    }, {
      field: 'value',
      title: '参数值',
      type: 'text'
    }/*, {
      field: 'description',
      title: '说明',
      type: 'text',
      readonly: true
    }*/
  ];

  ngOnChanges() {
    this.paramsData = {rows: this.task ? this.task.params : []};
  }
}



