import { Component, OnInit, Input, ElementRef, Renderer } from '@angular/core';
import { FormService } from '../../../service/form.service';
import { ControlTreeService } from '../../../service/control-tree.service';
import { DgBasicComponent } from '../../dg-basic/dg-basic.component';

@Component({
  selector: 'app-dg-button-group',
  templateUrl: './dg-button-group.component.html',
  styleUrls: ['./dg-button-group.component.css']
})
export class DgButtonGroupComponent extends DgBasicComponent implements OnInit {
  @Input() buttonGroup;

  constructor(public el: ElementRef, public renderer: Renderer,
    public formService: FormService, public controlTreeService: ControlTreeService) {
    super(el, renderer, formService, controlTreeService);
  }

  ngOnInit() {
    if (!this.buttonGroup) {
      return;
    }
    const appearance = this.buttonGroup['appearance'];
    if (appearance && appearance.class) {
      this.renderer.setElementClass(this.el.nativeElement, appearance.class, true);
    }
  }

  /**
   * 触发属性面板展示
   */
  showProperty() {
    this.formService.createPropertyPanel(this.buttonGroup);
    return false;
  }

  /**
   * 触发控件树focus
   */
  triggerTreeFocus() {
    this.controlTreeService.triggerTreeFocus.next(this.buttonGroup.id);
  }

}



