import { Component, OnInit, Input, HostListener } from '@angular/core';
import { ElementRef, Renderer } from '@angular/core';
import { FormService } from '../../../service/form.service';
import { ControlTreeService } from '../../../service/control-tree.service';
import { ViewModelService } from '../../../service/viewmodel.service';

@Component({
    selector: 'app-dg-grid',
    templateUrl: './dg-grid.component.html',
    styleUrls: ['./dg-grid.component.css']
})
export class DgGridComponent implements OnInit {
    @Input() grid;

    @HostListener('click', ['$event'])
    onClick($event) {
        if ($event) {
            $event.stopPropagation();
        }
        const elements = document.getElementsByClassName('dgComponentSelected');
        if (elements && elements.length > 0) {
            elements[0].classList.remove('dgComponentSelected');
        }

        if ($event && $event.target.className.startsWith('k-header')) { // 点击表头列
            const colIndex = $event.target.attributes['aria-colindex'].value;
            const fieldData = this.grid.fields[colIndex - 1];
            this.formService.createPropertyPanel(fieldData);
            $event.target.classList.add('dgComponentSelected');
        } else { // 点击整个表格
            this.showProperty();
            if ($event) {
                this.triggerTreeFocus();
            }
            this.renderer.setElementClass(this.el.nativeElement, 'dgComponentSelected', true);
        }
    }
    constructor(public el: ElementRef, public renderer: Renderer, private formService: FormService,
        private viewModelService: ViewModelService, public controlTreeService: ControlTreeService) {
        // super(el, renderer, formService, controlTreeService);
    }

    ngOnInit() {
        if (!this.grid) {
            return;
        }
        const appearance = this.grid['appearance'];
        if (appearance && appearance.class) {
            this.renderer.setElementClass(this.el.nativeElement, appearance.class, true);
        }
    }

    /**
       * 触发属性面板展示
       */
    showProperty() {
        const treeData = this.viewModelService.getChildVMTree(this.grid.dataSource);
        this.formService.createGridPropertyPanel(this.grid, treeData);
        return false;
    }

    /**
     * 触发控件树focus
     */
    triggerTreeFocus() {
        this.controlTreeService.triggerTreeFocus.next(this.grid.id);
    }

}



