import { TaskDesignerComponent } from './../task-designer/task-designer.component';
/**
 * 绘制一个容器，可以将指定的template内容放入，根据先后顺序，只显示最后一次放入的内容。
 * 内部记录放入的顺序，关闭最后一次放入的数据自动显示上一次的内容。
 * -----!
 */
import {
  Injectable, ComponentFactoryResolver, OnInit, Injector, ApplicationRef,
  ComponentRef, TemplateRef, ViewRef, ViewContainerRef, ChangeDetectorRef, EventEmitter
} from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { CommandDesignerComponent } from '../command-designer/command-designer.component';
// import { CommandListComponent } from '../command-list/command-list.component';
// import { CommandContainerComponent } from './command-container.component';

@Injectable()
export class EasyContainerService {
  private _applicationRef: ApplicationRef;
  private _cfr: ComponentFactoryResolver;
  // private _componentRef: ComponentRef<any>;
  private _containerRef: ViewContainerRef;

  private _viewHistory = new Array<any>();
  private _templates = new Map<string, TemplateRef<any>>();
  private _valueEmitters = new Array<EventEmitter<any>>();
  // 关于监听值改变的EventEmitter，界面A打开界面B，A关心的是B界面的值改变，而不是A，需要注意当前的EventEmitter是跳转前还是跳转后界面的。
  private _valueChanged: EventEmitter<any>;
  private _subscriptions: Array<Subscription>;
  private _originData: any;
  private _contentData: any;
  private _currentComponent: CommandDesignerComponent | TaskDesignerComponent;

  title: string;
  ///////////////////
  // 内置视图参数
  commandListContent: any;
  commandDesignerContent: any;
  taskListContent: any;
  taskDesignerContent: any;
  ///////////////////

  constructor(private injector: Injector) {
    this.title = '';

    this._cfr = this.injector.get(ComponentFactoryResolver);
    this._applicationRef = this.injector.get(ApplicationRef);

    // 构造时先创造出容器
    // const componentFactory = this._cfr.resolveComponentFactory(CommandContainerComponent);
    // this._componentRef = componentFactory.create(this.injector);
    // this._applicationRef.attachView(this._componentRef.hostView);
    // this._containerRef = (<CommandContainerComponent>this._componentRef.instance).container;
  }

  setContainer(container: ViewContainerRef) {
    this._containerRef = container;
  }

  attachTemplate(template: TemplateRef<any>, name: string) {
    this._templates.set(name, template);
  }

  // 按钮方法
  clickOk() {
    if (this._currentComponent) {
      this._currentComponent.clickOk();
    }
  }

  clickCancel() {
    if (this._currentComponent) {
      this._currentComponent.clickCancel();
    }
  }

  show(name: string, data: any, callback?: (data: any) => void) {
    this._checkContainerExist();

    // 显示新的视图
    const content = this._templates.get(name);
    if (!content) {
      console.warn('试图显示的模板名称没有添加到服务中。');
      return;
    }

    this._originData = data.contentData;
    // 深复制编辑的数据
    // this._contentData = this._originData ? JSON.parse(JSON.stringify(this._originData)) : undefined;
    const clone = new this._originData.constructor();
    this._contentData = Object.assign(clone, this._originData);

    switch (name) {
      case 'command-list':
        this.commandListContent = this._contentData;
        break;
      case 'command-designer':
        this.commandDesignerContent = this._contentData;
        break;
      case 'task-designer':
        this.taskDesignerContent = this._contentData;
        break;
      default:
        break;
    }
    this._valueChanged = new EventEmitter<any>();
    this._subscriptions = new Array<Subscription>();

    if (callback) {
      this._subscriptions.push(this._valueChanged.subscribe(callback));
    }

    setTimeout(() => {
      // 如果有正在显示的视图，需要先隐藏
      if (this._viewHistory && this._viewHistory.length) {
        this._containerRef.detach();
        this._currentComponent = void 0;
      }
      const activated = this._containerRef.createEmbeddedView(content);
      this._currentComponent = activated.rootNodes[0];
      this.title = data.title;

      this._viewHistory.push({
        name,
        contentData: this._contentData,
        originData: this._originData,
        emitter: this._valueChanged,
        subscriptions: this._subscriptions
      });
      // this._componentRef.changeDetectorRef.markForCheck();
    });
  }

  showPrevious() {
    if (this._viewHistory.length === 1) {
      console.error('历史记录为空，无法显示上一界面。');
      console.log('==============输出结果==============');
      return;
    }

    // 历史记录里清除上次记录
    const removed = this._viewHistory.pop();
    // 释放subscription
    for (const subscription of removed.subscriptions) {
      subscription.unsubscribe();
    }
    removed.emitter = null;

    const previous = this._viewHistory[this._viewHistory.length - 1];
    this._contentData = previous.contentData;
    this._originData = previous.originData;
    this._valueChanged = previous.emitter;

    this._innerShow(previous.name, false);
  }

  confirmToPrevious() {
    // Object.assign(this._editingData, )
    const data = this._contentData;
    this._valueChanged.emit(data);
    Object.assign(this._originData, this._contentData);
    this.showPrevious();
  }

  observeEditing(callback: (data: any) => void) {
    const subscription = this._valueChanged.subscribe((data: any) => {
      callback(data);
      subscription.unsubscribe();
    });
    this._viewHistory[this._viewHistory.length - 1].subscriptions.push(subscription);
  }

  /**
   * 展示一个界面
   * name: 需要打开的界面的名称
   * isNew：表示是否是新打开一个界面，还是回退到之前的界面。
   */
  private _innerShow(name: string, isNew: boolean) {
    this._checkContainerExist();

    // 显示新的视图
    const content = this._templates.get(name);
    if (!content) {
      console.warn('试图显示的模板名称没有添加到服务中。');
      return;
    }

    setTimeout(() => {
      // 如果有正在显示的界面需要先隐藏
      if (this._viewHistory && this._viewHistory.length) {
        this._containerRef.detach();
      }
      const activated = this._containerRef.createEmbeddedView(content);
      // this._containerRef.insert()
      // this.title = title;
      if (isNew) {
        this._viewHistory.push({
          name,
          contentData: this._contentData,
          emitter: this._valueChanged,
          subscriptions: new Array<Subscription>()
        });
      }

      // this._componentRef.changeDetectorRef.markForCheck();
    });
  }

  // private _getContentViewRef(content: TemplateRef<any>): ViewRef {
  //   const viewRef = content.createEmbeddedView({});
  //   this._applicationRef.attachView(viewRef);

  //   return viewRef;
  // }

  private _checkContainerExist() {
    if (!this._containerRef) {
      throw Error('CommandContainer没有完成初始化，无法完成show方法。');
    }
  }
}



