import { Component, OnInit, Input } from '@angular/core';
import { ElementRef, Renderer } from '@angular/core';
import { DesignElement } from '../../../entity/entity';
import { FormService } from '../../../service/form.service';
import { ControlTreeService } from '../../../service/control-tree.service';
import { DgBasicComponent } from '../../dg-basic/dg-basic.component';

@Component({
  selector: 'app-dg-button',
  templateUrl: './dg-button.component.html',
  styleUrls: ['./dg-button.component.css'],
})
export class DgButtonComponent extends DgBasicComponent implements OnInit {
  @Input() button: DesignElement;

  constructor(public el: ElementRef, public renderer: Renderer,
    public formService: FormService, public controlTreeService: ControlTreeService) {
    super(el, renderer, formService, controlTreeService);
  }

  ngOnInit() {
    if (!this.button) {
      return;
    }
    const appearance = this.button['appearance'];
    if (appearance && appearance.class) {
      this.renderer.setElementClass(this.el.nativeElement, appearance.class, true);

    }
  }

  /**
   * 触发属性面板展示
   */
  showProperty() {
    this.formService.createPropertyPanel(this.button);
    return false;
  }

  /**
   * 触发控件树focus
   */
  triggerTreeFocus() {
    this.controlTreeService.triggerTreeFocus.next(this.button.id);
  }
}



