import { Component, Output, EventEmitter, Input, OnChanges, OnInit, ElementRef } from '@angular/core';

@Component({
  selector: 'app-context-menu',
  templateUrl: './context-menu.component.html',
  styleUrls: ['./context-menu.component.css']
})
export class ContextMenuComponent implements OnInit {

  @Input() nodeData: any;
  @Output() rightMenuClicked = new EventEmitter<any>();
  addMenuDisable = false; // 新增控件是否置灰

  contentWidth = 0;
  constructor(private el: ElementRef) { }

  ngOnInit() {
    if (this.nodeData && this.nodeData.rawElement) {
      this.addMenuDisable = this.nodeData.rawElement.type === 'ContentContainer' ? false : true;
    }
    this.initContentWidth();
  }

  /**
   * 删除控件
   */
  deleteMenuClicked(event) {
    event.menuId = 'deleteControl';
    this.rightMenuClicked.emit(event);

  }
  addMenuClicked(event) {
    if (this.addMenuDisable) {
      return;
    }
    event.menuId = 'addControl';
    this.rightMenuClicked.emit(event);
  }

  // 树节点选中的宽度效果
  private initContentWidth() {
    const offset_left = this.el.nativeElement.offsetLeft;
    if (this.el.nativeElement.offsetParent) {
      const containerWidth = this.el.nativeElement.offsetParent.clientWidth - 20;
      this.contentWidth = containerWidth - offset_left;
    }
  }
}



