import { Component, OnInit, ViewChild, Renderer2, ComponentFactoryResolver, Injector, ViewContainerRef, ComponentRef } from '@angular/core';
import { GridComponent } from '@progress/kendo-angular-grid';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { fieldsMock } from './fields-mock';
import { VMField } from '../entity/field';
import { GSPElementDataTypes } from '../entity/GSPElementDataType';
import { Guid } from '../../../../util/Guid';
import { SelectVmFieldsComponent } from '../select-vm-fields/select-vm-fields.component';
import { BsModalService } from '@farris/ui';
import { PropertyPanelComponent, ElementPropertyConfig } from '@farris/ide-devkit';

@Component({
  selector: 'app-field-manager',
  templateUrl: './field-manager.component.html',
  styleUrls: ['./field-manager.component.css']
})
export class FieldManagerComponent implements OnInit {
  @ViewChild(GridComponent) private grid: GridComponent;
  public formGroup: FormGroup;
  fieldList = fieldsMock;
  dataTypes = GSPElementDataTypes;
  eleSelection: any[] = [];
  editedRowIndex;
  private isNewRow = false;
  showPropertyPanel = false;
  @ViewChild('propertyPanel', { read: ViewContainerRef }) propertyContainer: ViewContainerRef;
  constructor(private formBuilder: FormBuilder, private renderer: Renderer2,
    private resolver: ComponentFactoryResolver, private modalService: BsModalService, private injector: Injector) { }

  ngOnInit() {
    // 获取已选的VM字段

    // 表格失去焦点时，保存新增行
    this.renderer.listen(
      'document', 'click', ({ target }) => {
        if (!this.isChildOf(target, 'k-grid') && this.isNewRow) {
          this.saveNewRow();
        }
      });
  }
  hasClass = (el, className) => new RegExp(className).test(el.className);

  isChildOf = (el, className) => {
    while (el && el.parentElement) {
      if (this.hasClass(el.parentElement, className)) {
        return true;
      }
      el = el.parentElement;
    }
    return false;
  }
  /**
   * 单击触发编辑单元格
   */
  cellClickHandler(args: any) {
    const { sender, rowIndex, columnIndex, column, dataItem, isEdited } = args;
    const propertyName = column.field;
    // 保存新增行
    if (this.isNewRow && !this.saveNewRow()) {
      return;
    }

    // TODO 判断是否是VM带出的字段？
    // 根据字段类型判断编辑方式：属性列在属性面板中编辑
    switch (propertyName) {
      // case 'code': {
      //   return;
      // }
      case 'extendProperty':
        this.triggerPropertyPanel(dataItem);
        this.showPropertyPanel = true;
        return;
      default:
        if (!isEdited) {
          this.editedRowIndex = rowIndex;
          this.formGroup = this.createFormGroup(dataItem);
          sender.editCell(rowIndex, columnIndex, this.formGroup);
          // sender.editRow(rowIndex, this.formGroup);
          this.eleSelection = [dataItem.id];
          this.showPropertyPanel = false;
          return;
        }
    }
  }
  /**
   * 单元格编辑结束
   */
  cellCloseHandler(args: any) {
    const { formGroup, dataItem, column } = args;
    const propertyName = column.field;

    if (!formGroup.controls[propertyName].valid) {  // 若当前字段验证不通过，则编辑器不失焦
      args.preventDefault();
    } else if (formGroup.dirty) {
      const newValue = formGroup.value[propertyName];

      const selectedRow = this.fieldList.find(field => field.id === dataItem.id);
      selectedRow[propertyName] = newValue;
    }
  }

  private createFormGroup(dataItem: any): FormGroup {
    return this.formBuilder.group({
      'id': dataItem.id,
      'code': [dataItem.code || '', Validators.required],
      'name': [dataItem.name || '', Validators.required],
      'dataType': dataItem.dataType,
      'defaultValue': dataItem.defaultValue,
      'require': dataItem.require,
      'extendProperty': dataItem.extendProperty
    });
  }
  /**
   * 新增虚拟字段
   */
  addVirtualElement() {
    if (!this.saveNewRow()) {
      return;
    }

    this.closeEditor();
    const newField = new VMField();
    newField.id = Guid.newGuid();
    this.fieldList.push(newField);
    this.editedRowIndex = this.fieldList.length - 1;
    this.formGroup = this.createFormGroup(newField);
    this.grid.editRow(this.editedRowIndex, this.formGroup);
    this.isNewRow = true;
    this.eleSelection = [newField.id];
    this.showPropertyPanel = true;
  }

  /**
  * 保存新增行
  */
  saveNewRow() {
    if (!this.formGroup) {
      return true;
    }
    if (!this.formGroup.valid) {
      return false;
    }
    const editedField = this.fieldList.find((field) => field.id === this.formGroup.value.id);
    Object.assign(editedField, this.formGroup.value);

    this.isNewRow = false;
    this.closeEditor();
    return true;
  }
  /**
   * 删除选中行
   */
  public removeSelectedElement() {
    if (this.eleSelection.length === 0) { alert('请先选中要删除的字段。'); } else {
      while (this.eleSelection.length !== 0) {
        const id = this.eleSelection.pop();
        // if (this.canRemove(id)) {
        //   this.viewObject.elements.removeByID(id);
        // }
        const index = this.fieldList.findIndex(field => field.id === id);
        this.fieldList.splice(index, 1);
      }


      this.isNewRow = this.isNewRow ? false : this.isNewRow;
      this.closeEditor();

    }
  }

  private closeEditor(): void {
    this.grid.closeRow(this.editedRowIndex);

    // this.isNew = false;
    this.editedRowIndex = undefined;
    this.formGroup = undefined;
  }
  addVMFields() {
    const compFactory = this.resolver.resolveComponentFactory(SelectVmFieldsComponent);
    const compRef = compFactory.create(this.injector);
    const fieldIdList = this.fieldList.map(field => field.id);
    compRef.instance.excludeFields = fieldIdList;
    const modalConfig = {
      title: '选择视图模型字段',
      width: 850,
      height: 550,
      showButtons: true,
      buttons: compRef.instance.modalFooter
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModal.subscribe(() => {
      dialog.close();
    });
    compRef.instance.addFields.subscribe((selectedFields) => {
      if (selectedFields.length > 0) { this.fieldList.push(...selectedFields); }
      dialog.close();
    });
  }


  /**
   * 上移
   */
  moveUp() {
    if (!this.eleSelection || this.eleSelection.length === 0) {
      alert('请选选择要移动的字段');
    }
    const index = this.fieldList.findIndex(field => field.id === this.eleSelection[0]);
    if (index < 1) {
      return;
    }
    const ele = this.fieldList[index];
    this.fieldList.splice(index, 1);
    this.fieldList.splice(index - 1, 0, ele);
  }
  /*
  * 下移
  */
  moveDown() {
    if (!this.eleSelection || this.eleSelection.length === 0) {
      alert('请选选择要移动的字段');
    }
    const index = this.fieldList.findIndex(field => field.id === this.eleSelection[0]);
    if (index === this.fieldList.length - 1) {
      return;
    }
    const ele = this.fieldList[index];
    this.fieldList.splice(index, 1);
    this.fieldList.splice(index + 1, 0, ele);
  }

  /**
   * 触发展示属性面板
   */
  private triggerPropertyPanel(dataItem): void {
    let propertyData = {}; const extendStr = dataItem.extendProperty;
    if (extendStr) {
      propertyData = JSON.parse(extendStr);
    }

    const propertyConfig = this.getPropertyConfig(propertyData);
    this.propertyContainer.clear();
    const itemPanel = this.resolver.resolveComponentFactory(PropertyPanelComponent);
    const propertyRef: ComponentRef<PropertyPanelComponent> = this.propertyContainer.createComponent(itemPanel);
    propertyRef.instance.propertyConfig = propertyConfig;
    propertyRef.instance.propertyData = propertyData;
    propertyRef.instance.width = 300;
    propertyRef.instance.propertyChanged.subscribe(changeObject => {
      propertyData[changeObject.propertyID] = changeObject.propertyValue;
      dataItem.extendProperty = JSON.stringify(propertyData);
    });
  }
  private getPropertyConfig(propertyData: object): any[] {

    const properties = [{
      propertyID: 'extend1',
      propertyName: '属性1',
      propertyType: 'string',
      visible: true
    }, {
      propertyID: 'extend2',
      propertyName: '属性2',
      propertyType: 'string',
      visible: true
    }];
    Object.keys(propertyData).forEach(element => {

    });
    const config: ElementPropertyConfig[] = [
      {
        categoryId: 'extendProperty',
        categoryName: '扩展属性',
        properties: properties
      }];
    return config;
  }
}



