import { GSP, Package } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector } from '@angular/core';
import { FormEditorComponent } from './views/form-editor/form-editor.component';
import { FormEditorPackageView } from './views/form-editor-package-view';


export class FormEditorPackage extends Package {
    constructor(gsp: GSP, private cfr: ComponentFactoryResolver, private injector: Injector) {
        super(gsp);
    }

    activate() {
        this.gsp.workspace.addOpener((uri: string) => this.openFormEditor(uri));
    }

    deactivate() {
    }

    openFormEditor(uri) {
        if (uri.endsWith('.webdev')) {
            const pkgView = new FormEditorPackageView({}, this.cfr, this.injector);
            const currentElement = pkgView.getElement().instance as FormEditorComponent;
            currentElement.metadataUri = uri;
            return {
                getTitle: () => '表单设计器-' + uri.slice(uri.lastIndexOf('/') + 1),
                getElement: () => pkgView.getElement()
            };
        }
    }
}





