import { EasyContainerService } from './../container/easy-container.service';
import { Component, Input } from '@angular/core';
import { CommandHandler, Task } from '../entity/command-object';

@Component({
  selector: 'app-command-designer',
  templateUrl: './command-designer.component.html',
  styleUrls: ['./command-designer.component.css']
})
export class CommandDesignerComponent {
  private _selectedTask: any;
  // private _lastSelectedTask: any;

  @Input() content: CommandHandler;

  params = [];
  paramColumns = [
    {
      field: 'name',
      title: '参数名称',
      type: 'text'
    }, {
      field: 'type',
      title: '参数类型',
      type: 'text'
    }, {
      field: 'required',
      title: '必填',
      type: 'boolean'
    }, {
      field: 'description',
      title: '说明',
      type: 'text'
    }
  ];

  constructor(private easyContainer: EasyContainerService) {

  }

  clickOk() {
    //
  }

  clickCancel() {
    //
  }

  handleTaskSelection({ dataItem }: any) {
    // console.log(dataItem.name);
    this._selectedTask = dataItem;
  }

  handleTaskUnselect() {
    // console.log('blur');
    // this._lastSelectedTask = this._selectedTask;
    // this._selectedTask = void 0;
  }

  addParam() {
    this.content.paramDesc.push({name: '', value: '', description: ''});
  }

  removeParam() {

  }

  addTaskAfter() {
    const currentNodeIndex = this.getCurrentTaskIndex();
    // this.content.tasks.splice(currentNodeIndex + 1, 0, { name: 'newTask' });
    const tasks = this.content.tasks;
    for (let i = tasks.length - 1; i > currentNodeIndex; i--) {
      tasks[i + 1] = tasks[i];
    }
    setTimeout(() => {
      tasks[currentNodeIndex + 1] = new Task();
    });
    setTimeout(() => {
      // tasks[currentNodeIndex + 1] = { name: 'newTask' };
    });
  }

  addTaskBefore() {
    let currentNodeIndex = this.getCurrentTaskIndex();
    if (currentNodeIndex < 0) {
      currentNodeIndex = 0;
    }
    // this.content.tasks.splice(currentNodeIndex + 1, 0, { name: 'newTask' });
    const tasks = this.content.tasks;
    for (let i = tasks.length - 1; i >= currentNodeIndex; i--) {
      tasks[i + 1] = tasks[i];
    }
    setTimeout(() => {
      tasks[currentNodeIndex] = new Task();
    });
    setTimeout(() => {
      // tasks[currentNodeIndex] = { name: 'newTask' };
    });
  }

  removeTask() {
    // todo: 删除任务节点  confirm first?
    const position = this.getCurrentTaskIndex();
    this.content.tasks.splice(position, 1);
  }

  edit() {
    const testTaskData = {
      code: 'loadData',
      name: '加载数据',
      service: 'DataService',
      method: 'loadData',
      params: [],
      extendable: ['before', 'after', 'replace']
    };
    if (!this._selectedTask) {
      console.error('没有选中任务节点，不能编辑');
      return;
    }
    // this._selectedTask = this._lastSelectedTask;
    this.easyContainer.show('task-designer', { title: '编辑任务', contentData: this._selectedTask });
    // this.easyContainer.observeEditing((data) => { console.log(data); });
  }

  replace() {
    if (!this._selectedTask) {
      console.error('没有选中任务节点，不能替换');
      return;
    }
    const newTask = new Task();
    this.easyContainer.show('task-designer', { title: '编辑任务', contentData: newTask }, (data) => this._taskReplacedCallback(data));
  }

  addTask(addAfter: boolean) {
    if (!this._selectedTask) {
      console.error('没有选中任务节点，不能新增');
      return;
    }
    const newTask = new Task();
    this.easyContainer.show('task-designer', { title: '编辑任务', contentData: newTask }, (data) => this._taskAddedCallback(data, addAfter));
  }

  private _taskChangedCallback(data) {
    // console.log(data);
    Object.assign(this._selectedTask, data);
  }

  private _taskReplacedCallback(data) {
    Object.assign(this._selectedTask, data);
  }

  private _taskAddedCallback(data: any, addAfter: boolean) {
    const currentNodeIndex = this.getCurrentTaskIndex();
    const position = addAfter ? currentNodeIndex + 1 : currentNodeIndex < 0 ? 0 : currentNodeIndex;
    const tasks = this.content.tasks;
    for (let i = tasks.length - 1; i >= position; i--) {
      tasks[i + 1] = tasks[i];
    }

    tasks[position] = data;
  }

  // 暂时只支持获取第一层次的节点位置
  private getCurrentTaskIndex(): number {
    return this.content.tasks.findIndex((item: any) => item === this._selectedTask);
  }

}



