import { Component, OnInit, Input, ElementRef, Renderer } from '@angular/core';
import { DgBasicComponent } from '../../dg-basic/dg-basic.component';
import { FormService } from '../../../service/form.service';
import { ControlTreeService } from '../../../service/control-tree.service';

@Component({
  selector: 'app-dg-response-layout',
  templateUrl: './dg-response-layout.component.html',
  styleUrls: ['./dg-response-layout.component.css']
})
export class DgResponseLayoutComponent extends DgBasicComponent implements OnInit {
  @Input() response;

  constructor(public el: ElementRef, public renderer: Renderer,
    public formService: FormService, public controlTreeService: ControlTreeService) {
    super(el, renderer, formService, controlTreeService);
  }
  ngOnInit() {
    if (!this.response) {
      return;
    }
    const appearance = this.response.appearance;
    if (appearance) {
      if (appearance.class) {
        this.renderer.setElementClass(this.el.nativeElement, appearance.class, true);
      }

    }
  }
  /**
     * 触发属性面板展示
   */
  showProperty() {
    this.formService.createPropertyPanel(this.response);
    return false;
  }

  /**
   * 触发控件树focus
   */
  triggerTreeFocus() {
    this.controlTreeService.triggerTreeFocus.next(this.response.id);
  }

}



