import { Component, OnInit, Input } from '@angular/core';
import { ElementRef, Renderer } from '@angular/core';
import { DgBasicComponent } from '../dg-basic/dg-basic.component';
import { ControlTreeService } from '../../service/control-tree.service';
import { FormService } from '../../service/form.service';

@Component({
  selector: 'app-dg-label',
  templateUrl: './dg-label.component.html',
  styleUrls: ['./dg-label.component.css']
})
export class DgLabelComponent extends DgBasicComponent implements OnInit {
  @Input() field;

  constructor(public el: ElementRef, public renderer: Renderer,
    public formService: FormService, public controlTreeService: ControlTreeService) {
    super(el, renderer, formService, controlTreeService);
  }
  ngOnInit() {
    if (!this.field) {
      return;
    }
    const font = this.field['font'];
    if (font && font.color) {
      this.renderer.setElementStyle(this.el.nativeElement, 'color', font.color);
    }
    if (font && font.size) {
      this.renderer.setElementStyle(this.el.nativeElement, 'font-size', font.size);
    }
    const appearance = this.field['appearance'];
    if (appearance && appearance.class) {
      this.renderer.setElementClass(this.el.nativeElement, appearance.class, true);
    }

  }
  /**
     * 触发属性面板展示
     */
  showProperty() {
    this.formService.createPropertyPanel(this.field);
    return false;
  }

  /**
   * 触发控件树focus
   */
  triggerTreeFocus() {
    this.controlTreeService.triggerTreeFocus.next(this.field.id);
  }

}



