import { Component, OnInit, Input, ViewChild, TemplateRef, EventEmitter, Output } from '@angular/core';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { fieldsMock } from './fields-mock';

@Component({
  selector: 'app-select-vm-fields',
  templateUrl: './select-vm-fields.component.html',
  styleUrls: ['./select-vm-fields.component.css']
})
export class SelectVmFieldsComponent implements OnInit {

  @Input() excludeFields = [];
  @Output() closeModal = new EventEmitter<any>();
  @Output() addFields = new EventEmitter<any>();
  @ViewChild('modalFooter') modalFooter: TemplateRef<any>;
  selectableSettings: SelectableSettings = {
    mode: 'multiple',
    checkboxOnly: true
  };
  elements = [];
  eleSelection = []; // 选中的字段ID
  constructor() { }

  ngOnInit() {
    // 查询VM所有字段
    this.elements = fieldsMock;
    this.elements = this.elements.filter(field => !this.excludeFields.includes(field.id));
  }

  clickOK() {
    const selectedFields = this.elements.filter(field => this.eleSelection.includes(field.id));
    this.addFields.next(selectedFields);
  }
  clickCancel() {
    this.closeModal.next();
  }
}



