import { Component, OnInit, Input } from '@angular/core';
import { ElementRef, Renderer } from '@angular/core';
import { FormService } from '../../../service/form.service';
import { DgBasicComponent } from '../../dg-basic/dg-basic.component';
import { ControlTreeService } from '../../../service/control-tree.service';

@Component({
  selector: 'app-dg-text-box',
  templateUrl: './dg-text-box.component.html',
  styleUrls: ['./dg-text-box.component.css']
})
export class DgTextBoxComponent extends DgBasicComponent implements OnInit {

  @Input() field;
  constructor(public el: ElementRef, public renderer: Renderer, public formService: FormService,
    public controlTreeService: ControlTreeService) {
    super(el, renderer, formService, controlTreeService);
  }

  ngOnInit() {
    if (!this.field) {
      return;
    }
    const appearance = this.field['appearance'];
    if (appearance && appearance.class) {
      appearance.class.split(' ').forEach(className => {
        this.renderer.setElementClass(this.el.nativeElement, className, true);
      });
    }
  }

  /**
     * 触发属性面板展示
   */
  showProperty() {
   this.formService.createPropertyPanel(this.field);
    return false;
  }

  /**
   * 触发控件树focus
   */
  triggerTreeFocus() {
    this.controlTreeService.triggerTreeFocus.next(this.field.id);
  }
}



