import { Component, Input, OnChanges, ViewChild, OnInit, TemplateRef, EventEmitter, Output } from '@angular/core';
import { TreeComponent } from '@farris/ide-devkit/components/ng2tree-common';
import { BsModalService } from '@farris/ui';
import { DesignElementCollection } from '../../../../../../entity/entity';
import { FormService } from '../../../../../../service/form.service';
import { ControlTreeService } from '../../../../../../service/control-tree.service';
import { ControlService } from '../../../../../../service/control.service';
import { cloneDeep } from 'lodash';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-control-tree-panel',
  templateUrl: './control-tree-panel.component.html',
  styleUrls: ['./control-tree-panel.component.css'],
  providers: [ControlService]
})
export class ControlTreePanelComponent implements OnInit, OnChanges {
  @Input() formData: DesignElementCollection; // 表单json数据
  treeNodes = []; // 树json数据
  treeConfig = {
    allowDrag: true,
    hasCustomContextMenu: true
  };
  @ViewChild(TreeComponent)
  private treeComponent: TreeComponent; // 树组件
  @ViewChild('addControlModal') addControlModal: TemplateRef<any>;
  private controlModal;
  controlList = []; // 可添加的控件列表
  addParentNode; // 添加控件的父节点
  searchName = ''; // 搜索控件内容
  @Output() refreshCodeEditor = new EventEmitter<any>();
  constructor(private controlService: ControlService, private formService: FormService,
    private controlTreeService: ControlTreeService, private modalService: BsModalService) { }

  ngOnInit() {
    this.controlTreeService.triggerTreeFocus.subscribe((elementId) => {
      this.treeComponent.searchAndLocationNodeById(elementId);
    });
  }
  ngOnChanges() {
    // 表单对象映射为树结构
    this.treeNodes = this.controlTreeService.mappingFormData2Tree(this.formData);
  }

  /**
   *  点击右键菜单项
   */
  onRightMenuClicked($event) {
    const menuId = $event.rawEvent.menuId;
    if (menuId === 'deleteControl') {
      this.deleteControl($event.node);
    } else if (menuId === 'addControl') {
      this.searchName = '';
      this.controlService.getControlList(this.searchName).subscribe(data => {
        this.controlList = data;
      });
      this.addParentNode = $event.node;
      this.controlModal = this.modalService.show(this.addControlModal, {
        title: '选择控件',
        width: 600,
        height: 400,
        showButtons: false
      });
    }
  }

  /**
   * 删除控件
   * @param node
   */
  deleteControl(node) {
    // 删除树节点
    this.controlTreeService.deleteNode(node);

    // 修改表单json结构
    this.formService.deleteControl(node.data);
  }

  /**
   * 新增控件
   * @param type 控件类型
   */
  addControl(type) {
    // 获取控件元数据
    const metadata = this.controlService.getControlMetaData(type);
    const dgMetadata = cloneDeep(metadata);
    dgMetadata['elementFocused'] = new Subject<any>();
    // 修改表单json结构: rawElement是表单对象的引用，append方法会触发formData 同步修改
    this.formService.addControl(this.addParentNode.data.rawElement, dgMetadata);

    // 增加树节点
    this.controlTreeService.addNode(this.addParentNode, dgMetadata);

    this.controlModal.close();
  }


  /**
   * 点击树节点后focus表单控件
   * @param $event
   */
  focusFormControl($event) {
    const rawElement = $event.node.rawElement;
    if (rawElement && rawElement.elementFocused) {
      rawElement.elementFocused.next();
      this.refreshCodeEditor.emit(rawElement);
    }
  }

  /**
   * 按名称搜索控件
   */
  searchControl() {
    this.controlService.getControlList(this.searchName).subscribe(data => {
      this.controlList = data;
    });
  }
}



