import { Component, Input, OnInit } from '@angular/core';
import { EasyContainerService } from '../container/easy-container.service';
import { Task } from '../entity/command-object';

@Component({
  selector: 'app-task-designer',
  templateUrl: './task-designer.component.html'
})
export class TaskDesignerComponent implements OnInit {
  task: any;
  @Input() content: Task;

  paramColumns: any[] = [
    {
      field: 'name',
      title: '参数名称',
      type: 'text',
      readonly: true
    }, {
      field: 'value',
      title: '参数值',
      type: 'text'
    }, {
      field: 'description',
      title: '说明',
      type: 'text',
      readonly: true
    }
  ];

  constructor(private easyContainer: EasyContainerService) {

  }

  ngOnInit() {
    // this.task = this.
  }

  clickOk() {
    //
  }

  clickCancel() {
    //
  }

  confirm() {
    // this.content.name = '加载数据11111';
    this.easyContainer.confirmToPrevious();
  }

  cancel() {
    this.easyContainer.showPrevious();
  }

}



