import { EditableGridComponent } from './editable-grid.component';
import { NgModule, ModuleWithProviders } from '@angular/core';
// import { EditService } from '@progress/kendo-angular-grid/dist/es2015/editing/edit.service';
import { GridModule } from '@progress/kendo-angular-grid';
import { CommonModule } from '@angular/common';
import { HttpClientModule, HttpClientJsonpModule } from '@angular/common/http';

@NgModule({
  imports: [CommonModule, GridModule, HttpClientModule, HttpClientJsonpModule ],
  declarations: [EditableGridComponent],
  providers: [],
  exports: [EditableGridComponent]
})
export class EditableGridModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: EditableGridModule,
      providers: []
    };
  }
}



