import { Component, OnInit, ViewChild } from '@angular/core';
import { FormService } from '../../service/form.service';
import { ControlTreeService } from '../../service/control-tree.service';
import { NotifyService, NotifyOptions, LoadingService } from '@farris/ui';
import { environment } from '../../../../environments/environment';
import { FormDesignerComponent } from '../tabs/form-designer/form-designer/form-designer.component';
import { ViewModelService } from '../../service/viewmodel.service';
@Component({
    selector: 'webide-form-editor',
    templateUrl: './form-editor.component.html',
    styleUrls: ['./form-editor.component.css'],
    providers: [FormService, ControlTreeService, NotifyService, ViewModelService]
})
export class FormEditorComponent implements OnInit {
    editorTab = 'formDesigner';
    domJson;
    private _metadataUri;
    set metadataUri(metadataUri: string) {
        this.formService.getFormData(metadataUri).subscribe(domJson => {
            this.domJson = domJson;
            this._metadataUri = metadataUri;
        });
    }
    @ViewChild(FormDesignerComponent) private formDesigner: FormDesignerComponent;
    constructor(private formService: FormService, private controlTreeService: ControlTreeService,
        private notifyService: NotifyService, private loadingService: LoadingService) { }
    ngOnInit() {

    }

    changeEditorBar(key) {
        this.editorTab = key;
    }

    /**
     * 保存表单
     */
    saveForm() {
        // console.log(this.formData);
        this.formDesigner.saveForm();
        this.formService.saveFormData(this.domJson).subscribe(() => {
            this.notifyService.success(<NotifyOptions>{
                title: '系统提示', msg: '保存表单成功！', timeout: 3000
            });

        }, error => {
            this.notifyService.error(<NotifyOptions>{
                title: '系统提示', msg: '保存表单失败！', timeout: 3000
            });
        });
    }

    /**
     * 运行：先保存再运行
     */
    runForm() {
        this.formDesigner.saveForm();
        this.formService.saveFormData(this.domJson).subscribe(() => {
            const uri = this._metadataUri.replace(/\\/g, '/');
            const fileName = uri.substring(uri.lastIndexOf('/') + 1);
            const filePath = uri.substring(1, uri.lastIndexOf('/'));
            const url = environment.SERVER_IP + '/api/dev/main/v1.0/jitanalysis/jit?metaDataFileName=' + fileName
                + '&metaDataFilePath=' + filePath;


            const loading = this.loadingService.show({ container: 'body' });

            this.formService.runForm(url).subscribe(data => {
                if (data) {
                    window.open(environment.SERVER_IP + '/platform/dev/main/web/webidedebug/dist/index.html');
                }
                loading.close();
            }, error => {
                loading.close();
                this.notifyService.error(<NotifyOptions>{
                    title: '系统提示', msg: '表单运行失败！', timeout: 3000
                });
            });
        });

    }

}



