import { CommandEditorComponent } from './command-editor/command-editor.component';
import { EditableGridModule } from './grid/editable-grid.module';
import { TaskRouterDesignerComponent } from './task-router/task-router-designer.component';
import { TaskFlowComponent } from './task-flow/task-flow.component';
import { TaskDesignerComponent } from './task-designer/task-designer.component';
import { NgModule } from '@angular/core';
import { GridModule } from '@progress/kendo-angular-grid';
import { NgbTabsetModule, TreeModule } from '@farris/ide-devkit';
import { PanelBarModule, LayoutModule } from '@progress/kendo-angular-layout';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { TreeViewModule } from '@progress/kendo-angular-treeview';
// import { TreeTableModule } from '@farris/ui';
import { CommonModule } from '@angular/common';
import { Serializer } from './serializer';
import { EasyContainerService } from './container/easy-container.service';
import { CommandDesignerComponent } from './command-designer/command-designer.component';
import { FormsModule } from '@angular/forms';
import { CommandPropertyComponent } from './property-panel/command-property.component';
import { TaskPropertyComponent } from './property-panel/task-property.component';
import { CommandEditorHelper } from './command-editor/command-editor.helper';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    GridModule,
    NgbTabsetModule.forRoot(),
    LayoutModule,
    PanelBarModule,
    InputsModule,
    TreeViewModule,
    // TreeTableModule,
    TreeModule,
    EditableGridModule.forRoot()
  ],
  declarations: [
    // CommandListComponent,
    CommandDesignerComponent,
    TaskFlowComponent,
    TaskDesignerComponent,
    TaskRouterDesignerComponent,
    // CommandComponent,
    CommandEditorComponent,
    CommandPropertyComponent,
    TaskPropertyComponent
  ],
  exports: [
    /*CommandListComponent, */CommandDesignerComponent, TaskFlowComponent, TaskDesignerComponent,
    TaskRouterDesignerComponent, /*CommandComponent,*/ CommandEditorComponent, CommandPropertyComponent, TaskPropertyComponent
  ],
  providers: [
    // CommandManager,
    Serializer,
    EasyContainerService,
    CommandEditorHelper
  ],
  entryComponents: [/*CommandComponent*/]
})
export class CommandDesignerModule {

}



