import { Component, OnInit, Renderer, ElementRef, Input } from '@angular/core';
import { FormService } from '../../../service/form.service';
import { DgBasicComponent } from '../../dg-basic/dg-basic.component';
import { ControlTreeService } from '../../../service/control-tree.service';

@Component({
  selector: 'app-dg-panel',
  templateUrl: './dg-panel.component.html',
  styleUrls: ['./dg-panel.component.css']
})
export class DgPanelComponent extends DgBasicComponent implements OnInit {
  @Input() panel;

  constructor(public el: ElementRef, public renderer: Renderer,
    public formService: FormService, public controlTreeService: ControlTreeService) {
    super(el, renderer, formService, controlTreeService);
  }
  ngOnInit() {
    if (!this.panel) {
      return;
    }
    const appearance = this.panel.appearance;
    if (appearance) {
      if (appearance.class) {
        this.renderer.setElementClass(this.el.nativeElement, appearance.class, true);
      }

    }
  }
  /**
     * 触发属性面板展示
   */
  showProperty() {
    this.formService.createPropertyPanel(this.panel);
    return false;
  }

  /**
   * 触发控件树focus
   */
  triggerTreeFocus() {
    this.controlTreeService.triggerTreeFocus.next(this.panel.id);
  }
}



