import { Injectable } from '@angular/core';
import { find } from 'lodash';
@Injectable()
export class ViewModelService {
    private _vmTreeData;

    constructor() { }


    /**
    * 获取模型tables树，schemas映射为treeView的数据格式
    */
    public getVMTree(domJson) {
        const schemas = domJson.module.schemas;
        if (!schemas || schemas.length === 0) {
            return;
        }
        const rootObject = {
            id: 'rootViewModel',
            name: '视图模型',
            code: '',
            isExpanded: true,
            children: []
        };
        rootObject.children.push(this.getTablesTree(schemas[0]));
        this._vmTreeData = [rootObject];
        return [rootObject];
    }

    private getTablesTree(dataModel: object) {
        const rootObject = {
            id: dataModel['id'],
            name: dataModel['name'],
            code: dataModel['code'],
            isExpanded: true,
            children: []
        };

        const childFields = dataModel['children'];   // 子表
        if (childFields && Object.keys(childFields).length > 0) {
            Object.keys(childFields).forEach(childField => {
                const childNode = this.getTablesTree(childFields[childField]);
                rootObject.children.push(childNode);
            });
        }

        return rootObject;
    }
    /**
    * 将模型映射为treeView的数据格式
    */
    public getViewModelTree(domJson) {
        const schemas = domJson.module.schemas;
        if (!schemas || schemas.length === 0) {
            return;
        }

        const rootObject = this.mappingDgVM2Tree(schemas[0]); // 目前只有一个对象
        this._vmTreeData = [rootObject];
        return [rootObject];
    }
    /**
     * 将DgViewModel映射为kendo-treeview格式
     */
    private mappingDgVM2Tree(dataModel: object) {
        const rootObject = {
            ID: dataModel['id'],
            Name: dataModel['name'],
            Code: dataModel['code'],
            labelID: dataModel['code'],
            Children: []
        };
        const fields = dataModel['fields'];
        Object.keys(fields).forEach(key => {
            let isLeaf = true;
            // 关联表
            const refObject = fields[key].refObject;
            let nodeRefObject;
            if (refObject) {
                nodeRefObject = this.mappingDgVM2Tree(refObject);
                isLeaf = false;
            }
            const nodeObject = {
                ID: fields[key].id,
                Name: fields[key].name || '',
                Code: key,
                labelID: fields[key].labelID
            };
            if (!isLeaf) {
                nodeObject['Children'] = [nodeRefObject];
            }

            rootObject.Children.push(nodeObject);
        });

        // 子表
        const childFields = dataModel['children'];
        if (childFields && Object.keys(childFields).length > 0) {
            const childRoot = {
                ID: '',
                Name: '子表',
                Code: '',
                labelID: ''
            };
            if (Object.keys(childFields).length > 0) {
                childRoot['Children'] = [];
                Object.keys(childFields).forEach(childField => {
                    const childObject = this.mappingDgVM2Tree(childFields[childField]);
                    childRoot['Children'].push(childObject);
                });
            }

            rootObject.Children.push(childRoot);
        }

        return rootObject;
    }


    /**
    * 获取子表的tree(暂时按一层子表处理，目前用于卡片模板的列编辑器)
    */
    getChildVMTree(dataSource) {

        const sourceList = dataSource.split('.');
        if (sourceList.length === 1) {
            return this._vmTreeData;
        }
        const children = this._vmTreeData[0].Children;
        if (!children || children.length === 0) {
            return {};
        }
        const childTreeList = find(children, function (o) { return o.Name === '子表' && o.Code === ''; });
        const childTree = find(childTreeList.Children, function (o) { return o.Code === sourceList[1]; });
        return [childTree];
    }
}



