import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DesignElementCollection } from '../entity/entity';
import { Subject } from 'rxjs/Subject';
import { ContextMenuComponent } from '../views/tabs/form-designer/panels/form-control-tree/context-menu/context-menu.component';

@Injectable()
export class ControlTreeService {
    triggerTreeFocus = new Subject<any>(); // 外部（表单编辑器）触发树节点的focus事件
    constructor(private http: HttpClient) {
    }

    /**
     * 新增树节点
     * @param parentNode 父节点
     * @param metadata 新增控件的元数据
     */
    addNode(parentNode, metadata) {
        const nodeObject = {
            id: metadata.id,
            name: metadata.type,
            rawElement: metadata,
            rawParentElement: parentNode.rawElement.contents,
            rawParentId: parentNode.rawElement.id,
            children: [],
            hasRightClickMenu: true,
            rightClickMenuComponent: ContextMenuComponent
        };
        parentNode.treeModel.createAndAddTreeNode(nodeObject, parentNode);
    }

    /**
     * 删除树节点
     * @param node
     */
    deleteNode(node) {
        node.treeModel.removeTreeNode(node);
    }

    /**
     * 表单对象映射为树结构
     * @param formData 表单Dg对象
     */
    mappingFormData2Tree(formData) {
        if (!formData) {
            return;
        }
        return this.recursionElements(formData, '');
    }
    private recursionElements(elementCollection: DesignElementCollection, parentId: String) {
        const nodeObjectList = [];
        const elements = elementCollection.elements;
        elements.forEach(element => {
            let isLeaf = true, nodeRefObject;
            if (element.contents) {
                isLeaf = false;
                nodeRefObject = this.recursionElements(element.contents, element.id);
            }
            const nodeObject = {
                id: element.id,
                name: element.type,
                rawElement: element, // rawElement是表单对象的引用
                rawParentElement: elementCollection,
                rawParentId: parentId,
                children: isLeaf ? [] : nodeRefObject,
                hasRightClickMenu: true,
                rightClickMenuComponent: ContextMenuComponent,
                isExpanded: true
            };
            nodeObjectList.push(nodeObject);
        });
        return nodeObjectList;
    }
}



