import { Injectable, ViewContainerRef, ComponentRef, ComponentFactoryResolver } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject, Observable } from 'rxjs';
import { DesignElementCollection, DesignElement } from '../entity/entity';
import { GSPMetadataService, MetadataDto } from '@gsp-lcm/metadata-selector';
import { find, cloneDeep, assign } from 'lodash';
import { GridFieldEditorComponent } from '../views/demo/grid-field-editor/grid-field-editor.component';
import { ElementPropertyConfig, TypeConverter, PropertyPanelComponent } from '@farris/ide-devkit';
import { IconSelectEditorComponent } from '../views/demo/icon-select-editor/icon-select-editor.component';

@Injectable()
export class FormService {
    private propertyContainer: ViewContainerRef;
    private _metadataDto: MetadataDto;
    private _vmTreeData;
    private _domDgMap = new Map<string, any>(); // 保存每层dom节点
    get domDgMap() {
        return this._domDgMap;
    }
    constructor(private http: HttpClient, private metadataService: GSPMetadataService,
        private componentFactoryResolvor: ComponentFactoryResolver) {
    }

    /**
     * 获取表单DOM描述并转化为DgElement描述
     */
    getFormData(metadataUri) {
        const result = new Subject<DesignElementCollection>();
        const uri = metadataUri.replace(/\\/g, '/');
        const fileName = uri.substring(uri.lastIndexOf('/') + 1);
        const filePath = uri.substring(1, uri.lastIndexOf('/'));
        this.metadataService.LoadMetadata(fileName, filePath).subscribe(data => {
            this._metadataDto = data;
            const metadataContent = JSON.parse(data.content);
            const domJson = JSON.parse(metadataContent.Contents);
            result.next(domJson);
        });
        return result;
    }

    /**
     * 表单DOM转化为DgElement描述
     * @param domJson
     */
    public mappingDom2DgForm(domJson) {
        const module = domJson.module;
        const { components, ...rootElement } = module;
        const componentElements = this.recursiveDOM(components);
        if (componentElements) {
            rootElement.contents = new DesignElementCollection(componentElements);
        }
        rootElement.elementFocused = new Subject<any>();
        const dgElementCollection = new DesignElementCollection([rootElement]);

        this._domDgMap.set(rootElement.id, domJson);
        return dgElementCollection;
    }
    private recursiveDOM(domElements) {
        if (!domElements || domElements.length === 0) {
            return [];
        }
        const dgElements = [];
        domElements.forEach(domElement => {
            const { contents, ...elementInfo } = domElement;
            if (contents) {
                const childDgElements = this.recursiveDOM(contents);
                elementInfo.contents = new DesignElementCollection(childDgElements);
            }
            elementInfo.elementFocused = new Subject<any>();
            dgElements.push(elementInfo);
            this.domDgMap.set(elementInfo.id, domElement);
        });
        return dgElements;

    }

    /**
     * 保存表单：表单对象映射为DOM，并保存元数据
     * @param dgForm
     */
    // saveFormData(dgForm: DesignElementCollection): Observable<any> {
    // const domJson = this.mappingForm2Json(dgForm);
    saveFormData(domJson): Observable<any> {
        const metadataContent = JSON.parse(this._metadataDto.content);
        metadataContent.Contents = JSON.stringify(domJson);
        this._metadataDto.content = JSON.stringify(metadataContent);
        return this.metadataService.SaveMetadata(this._metadataDto, '');
    }
    private mappingForm2Json(dgForm) {
        if (!dgForm.elements || dgForm.elements.length === 0) {
            return null;
        }
        const rootDomJson = { module: {} };
        const rootModuleElement = dgForm.elements[0]; // 默认module 只有一个
        const { contents, elementFocused, ...rootModule } = rootModuleElement;
        const childcomponents = this.recursiveDgContents(contents);
        rootModule.components = childcomponents;
        rootDomJson.module = rootModule;

        return rootDomJson;

    }
    private recursiveDgContents(dgContents) {
        if (!dgContents.elements || dgContents.elements.length === 0) {
            return null;
        }
        const childJsonList = [];
        dgContents.elements.forEach(element => {
            const { contents, elementFocused, ...childJson } = element;
            if (contents) {
                const childcontents = this.recursiveDgContents(contents);
                childJson['contents'] = childcontents;
            }
            childJsonList.push(childJson);
        });
        return childJsonList;
    }

    /**
     * 新增控件
     * @param element 父节点
     * @param metadata 新增控件的元数据
     */
    addControl(element: DesignElement, dgMetadata) {
        if (!element) {
            return;
        }
        const { elementFocused, ...metadata } = dgMetadata;
        // 触发map 和domjson修改
        this._domDgMap.set(metadata.id, metadata);
        this._domDgMap.get(element.id).contents.push(metadata);

        // 触发formData 修改

        element.contents.append(dgMetadata);

    }
    /**
     * 删除控件
     */
    deleteControl(nodeData) {
        const parentElement: DesignElementCollection = nodeData.rawParentElement,
            deleteElement: DesignElement = nodeData.rawElement,
            parentElementId = nodeData.rawParentId;
        // 触发formData 修改
        if (parentElement) {
            const elements = parentElement.elements;
            for (let index = 0; index < elements.length; index++) {
                const element = elements[index];
                if (element.id === deleteElement.id) { // 查找当前节点在父节点中的位置
                    parentElement.remove(index);
                    break;
                }
            }
        }
        // 触发map 和domjson修改
        if (parentElementId) {
            const parentDom = this._domDgMap.get(parentElementId);
            for (let index = 0; index < parentDom.contents.length; index++) {
                if (parentDom.contents[index].id === deleteElement.id) {
                    parentDom.contents.splice(index, 1);
                    this._domDgMap.delete(deleteElement.id);
                    break;
                }

            }
        }
    }


    runForm(url) {
        return this.http.get(url);
    }

    /**
     * 初始化属性面板
     * @param container
     */
    initPropertyContainer(container) {
        this.propertyContainer = container;
    }

    /**
     * 触发展示属性面板
     * @param propertyData
     */
    createPropertyPanel(propertyData) {
        if (!propertyData || !propertyData.type) {
            return;
        }
        const propertyConfig = this.getProperConfig(propertyData);
        if (!propertyConfig) {
            return;
        }
        this.propertyContainer.clear();
        const itemPanel = this.componentFactoryResolvor.resolveComponentFactory(PropertyPanelComponent);
        const propertyRef: ComponentRef<PropertyPanelComponent> = this.propertyContainer.createComponent(itemPanel);
        propertyRef.instance.propertyConfig = propertyConfig;
        propertyRef.instance.propertyData = propertyData;
        propertyRef.instance.width = 300;
        propertyRef.instance.propertyChanged.subscribe(data => {
            this.setDomDgMap(propertyData.id, data);
        });
    }

    public setDomDgMap(id, changeObject) {
        const domJson = this._domDgMap.get(id);
        domJson[changeObject.propertyID] = changeObject.propertyValue;
    }

    /**
     * 组装属性配置（mock）
     */
    createGridPropertyPanel(propertyData, treeData) {
        if (!propertyData || !propertyData.type) {
            return;
        }
        const propertyConfig: ElementPropertyConfig[] = [
            {
                categoryId: 'usual',
                categoryName: '常规',
                properties: [{
                    propertyID: 'id',
                    propertyName: '标识',
                    propertyType: 'string',
                    visible: true,
                    readonly: true
                }, {
                    propertyID: 'caption',
                    propertyName: '标题',
                    propertyType: 'string',
                    visible: true
                }, {
                    propertyID: 'fields',
                    propertyName: '列',
                    propertyType: 'modal',
                    editor: GridFieldEditorComponent,
                    editorParams: {
                        treeData: treeData
                    },
                    visible: true,
                    converter: new GridFieldConverter()
                }, {
                    propertyID: 'appearance',
                    propertyName: 'appearance',
                    propertyType: 'modal',
                    visible: true
                }, {
                    propertyID: 'border',
                    propertyName: '边线',
                    propertyType: 'modal',
                    visible: true
                }, {
                    propertyID: 'font',
                    propertyName: '字体',
                    propertyType: 'modal',
                    visible: true
                }, {
                    propertyID: 'margin',
                    propertyName: '外边距',
                    propertyType: 'modal',
                    visible: true
                }, {
                    propertyID: 'padding',
                    propertyName: '内边距',
                    propertyType: 'modal',
                    visible: true
                }, {
                    propertyID: 'size',
                    propertyName: '大小',
                    propertyType: 'modal',
                    visible: true
                }
                ]
            }];

        this.propertyContainer.clear();
        const itemPanel = this.componentFactoryResolvor.resolveComponentFactory(PropertyPanelComponent);
        const propertyRef: ComponentRef<PropertyPanelComponent> = this.propertyContainer.createComponent(itemPanel);
        propertyRef.instance.propertyConfig = propertyConfig;
        propertyRef.instance.propertyData = propertyData;
        propertyRef.instance.width = 300;
    }

    /**
     * 组装属性配置（mock）
     */
    private getProperConfig(formObject) {
        if (!formObject) {
            return null;
        }
        const properties = [];
        Object.keys(formObject).forEach(key => {
            const property = this.getPropertyByKey(key);
            if (property) {
                properties.push(property);
            }
        });
        const Config: ElementPropertyConfig[] = [
            {
                categoryId: 'usual',
                categoryName: '常规',
                properties: properties
            },
            {
                categoryId: 'commands',
                categoryName: '事件',
                properties: []
            }];
        return Config;
    }

    private getPropertyByKey(key) {
        const propertyDic = {
            id: {
                propertyID: 'id',
                propertyName: '标识',
                propertyType: 'string',
                visible: true,
                readonly: true
            },
            placeHolder: {
                propertyID: 'placeHolder',
                propertyName: 'Placeholder',
                propertyType: 'string',
                visible: true
            },
            caption: {
                propertyID: 'caption',
                propertyName: '标题',
                propertyType: 'string',
                visible: true
            },
            fields: {
                propertyID: 'fields',
                propertyName: '列',
                propertyType: 'modal',
                editor: GridFieldEditorComponent,
                editorParams: {
                    treeData: {}
                },
                visible: true
            },
            appearance: {
                propertyID: 'appearance',
                propertyName: 'appearance',
                propertyType: 'modal',
                visible: true
            },
            border: {
                propertyID: 'border',
                propertyName: '边线',
                propertyType: 'modal',
                visible: true
            },
            font: {
                propertyID: 'font',
                propertyName: '字体',
                propertyType: 'modal',
                visible: true
            },
            margin: {
                propertyID: 'margin',
                propertyName: '外边距',
                propertyType: 'modal',
                visible: true
            },
            padding: {
                propertyID: 'padding',
                propertyName: '内边距',
                propertyType: 'modal',
                visible: true
            },
            size: {
                propertyID: 'size',
                propertyName: '大小',
                propertyType: 'modal',
                visible: true
            },
            icon: {
                propertyID: 'icon',
                propertyName: '图标',
                propertyType: 'modal',
                visible: true,
                editor: IconSelectEditorComponent
            },
            binding: {
                propertyID: 'binding',
                propertyName: '绑定',
                propertyType: 'modal',
                visible: true
            },
            title: {
                propertyID: 'title',
                propertyName: '标签',
                propertyType: 'string',
                visible: true
            },
            readonly: {
                propertyID: 'readonly',
                propertyName: '是否只读',
                propertyType: 'boolean',
                visible: true
            },
            require: {
                propertyID: 'require',
                propertyName: '是否必填',
                propertyType: 'boolean',
                visible: true
            },
            disable: {
                propertyID: 'disable',
                propertyName: '是否不可用',
                propertyType: 'boolean',
                visible: true
            },
            format: {
                propertyID: 'format',
                propertyName: '格式',
                propertyType: 'modal',
                visible: true
            },
            validation: {
                propertyID: 'validation',
                propertyName: '校验',
                propertyType: 'modal',
                visible: true
            },
            defaultValue: {
                propertyID: 'defaultValue',
                propertyName: '默认值',
                propertyType: 'string',
                visible: true
            },
            value: {
                propertyID: 'value',
                propertyName: '取值',
                propertyType: 'string',
                visible: true
            },
            maxLength: {
                propertyID: 'maxLength',
                propertyName: '取值最大长度',
                propertyType: 'number',
                precision: 0,
                min: 3,
                max: 10,
                visible: true
            },
            text: {
                propertyID: 'text',
                propertyName: '文本',
                propertyType: 'string',
                visible: true
            },
            numberValue: {
                propertyID: 'numberValue',
                propertyName: '取值',
                propertyType: 'number',
                precision: '3',
                min: 3,
                max: 10,
                visible: true
            }
        };
        return propertyDic[key];
    }

}


class GridFieldConverter implements TypeConverter {

    constructor() { }
    convertTo(data): string {
        return '共 ' + data.length + ' 列';

    }
}



