import { CommandHandler, Task, Command } from '../entity/command-object';
import { Injectable } from '@angular/core';

@Injectable()
export class CommandEditorHelper {

  addTask(task: Task, target: CommandHandler, ) {
    target.tasks.unshift(task);
  }
}



